.main {
  margin-top: 56px;
  padding: 5px 25px;
  padding-bottom: 25px;
}

.text {
  display: none;
}

@media (min-width: 768px) {
  .main {
    margin: 0;
    padding: 0 35px;
    padding-bottom: 50px;
  }

  .wrapper {
    position: absolute;
    z-index: 400;
    top: 180px;
  }

  .title {
    margin-bottom: 20px;
    padding: 0 10px;
    left: 35px;
    font-size: 50px;
    color: #f7c90b;
    background-color: rgba(0, 0, 0, 0.5);
  }

  .text {
    padding: 0 10px;
    display: inline;
    color: #f7c90b;
    background-color: rgba(0, 0, 0, 0.5);
  }
}